
.loadingWrap .loading {
  width: 32px;
  height: 32px;
  display: block;
  margin: 0 auto;
  -webkit-animation: loading 2s infinite linear;
          animation: loading 2s infinite linear;
}
@-webkit-keyframes loading {
0% {
    -webkit-transform: rotate(0);
            transform: rotate(0);
}
100% {
    -webkit-transform: rotate(720deg);
            transform: rotate(720deg);
}
}
@keyframes loading {
0% {
    -webkit-transform: rotate(0);
            transform: rotate(0);
}
100% {
    -webkit-transform: rotate(720deg);
            transform: rotate(720deg);
}
}
.loadingWrap p {
  text-align: center;
}


