
.topbar-wrapper {
  width: 100%;
  height: 30px;
  line-height: 30px;
  background: #d81e06;
  color: #fff;
  font-size: 14px;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999;
}
.topbar-wrapper .back {
  position: absolute;
  top: 0;
  left: 0;
  padding: 0 15px;
  font-size: 16px;
}
.topbar-wrapper .title {
  text-align: center;
  width: 100%;
}


