
a {
  text-decoration: none;
}
.nav-bar {
  background: #ccc;
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
}
.nav-item {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  text-align: center;
}
.nav-item .nav-item-icon {
  width: 24px;
  height: 24px;
  display: inline-block;
  background-size: 100%;
}
.nav-item-icon1 {
  background: url("../static/img/home.png") no-repeat center;
}
.nav-item-icon2 {
  background: url("../static/img/movie.png") no-repeat center;
}
.nav-text {
  text-decoration: none;
  display: block;
  font-size: 12px;
  color: #fff;
}
.router-link-active {
  color: #d81e06;
}
.router-link-active .nav-item-icon1 {
  background: url("../static/img/home-orange.png") no-repeat center;
  background-size: 100%;
}
.router-link-active .nav-item-icon2 {
  background: url("../static/img/movie-orange.png") no-repeat center;
  background-size: 100%;
}
.clearfix:before, .clearfix:after {
  content: '';
  display: table;
}
.clearfix:after {
  clear: both;
}



